/* doctorDashboard.css */

/* Container styling */
.doctor-dashboard-container {
    max-width: 900px;
    margin: 0 auto;
    padding: 20px;
    background-color: #f9f9f9;
    border-radius: 8px;
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);
    font-family: Arial, sans-serif;
}

/* Heading styling */
.doctor-dashboard-container h1 {
    font-size: 28px;
    color: #333;
    text-align: center;
    margin-bottom: 20px;
}

/* Section styling */
.dashboard-sections {
    display: flex;
    flex-direction: column;
    gap: 30px;
}

.appointment-section,
.patient-review-section {
    background-color: #fff;
    padding: 20px;
    border-radius: 8px;
    box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.05);
}

.appointment-section h2,
.patient-review-section h2 {
    font-size: 24px;
    color: #555;
    margin-bottom: 15px;
}

/* Input styling */
.dashboard-sections input[type="text"] {
    width: 100%;
    padding: 10px;
    margin-bottom: 10px;
    border: 1px solid #ccc;
    border-radius: 4px;
    font-size: 16px;
    box-sizing: border-box;
    transition: border-color 0.3s;
}

.dashboard-sections input[type="text"]:focus {
    border-color: #007bff;
    outline: none;
}

/* Button styling */
button {
    display: block;
    width: 100%;
    padding: 12px;
    background-color: #007bff;
    color: #fff;
    border: none;
    border-radius: 4px;
    font-size: 18px;
    cursor: pointer;
    margin-top: 20px;
    transition: background-color 0.3s;
}

button:hover {
    background-color: #0056b3;
}
